.lockscreen-wrapper {
  position: absolute;
  display: block;
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  background: url("../../img/lockscreen.jpg") no-repeat center center fixed;
  background-size: cover;
}

.lockscreen-wrapper .lockscreen {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.lockscreen-wrapper .locksreen-logo {
  font-size: 48px;
  margin-bottom: 32px;
}

.lockscreen-wrapper .locksreen-logo .locksreen-logo-text {
  text-shadow: 0px 5px 5px rgba(0, 0, 0, 0.5);
}

.lockscreen-wrapper .locksreen-logo .locksreen-logo-image {
  margin-right: 12px;
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.5);
}

.lockscreen-wrapper .panel {
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.5);
}

.lockscreen-wrapper .panel input:first-child {
  border-radius: 4px 4px 0  0;
  border-bottom: 0px;
}

.lockscreen-wrapper .panel input:nth-child(2) {
  border-radius: 0  0 4px 4px;
}

.lockscreen-wrapper .form-control {
  color: #333333 !important;
}
